import { __ } from 'actions/types';

const initialState = {
  items: [],
  errorMessage: null,
};

export default (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case __.SUCCESS:
      return { items: [...state.items, ...payload] };

    case __.FAILURE:
      return { errorMessage: payload };

    default:
      return state;
  }
};
