import { __ } from 'actions/types';

const initialState = {
  item: {},
  errorMessage: null,
};

export default (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case __.SUCCESS:
      return { item: { ...state.item, ...payload } };

    case __.FAILURE:
      return { errorMessage: payload };

    default:
      return state;
  }
};
