import { _ } from 'actions/types';

const initialState = {
  visible: false,
  message: '',
};

export default (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case _.PUSH_MESSAGE:
      return { visible: true, message: payload };

    case _.HIDE_MESSAGE:
      return { visible: false, message: '' };
    default:
      return state;
  }
};
