import { _ } from './types';

class Snackbar {
  constructor() {
    this.timerId = '';
  }

  pushMessageSnackbar = (message) => (dispatch) => {
    if (this.timerId) {
      clearTimeout(this.timerId);
    }

    dispatch({ type: _.PUSH_MESSAGE, payload: message });

    this.timerId = setTimeout(() => {
      dispatch({ type: _.HIDE_MESSAGE });
      this.timerId = null;
    }, 3000);
  };
}

const snackbar = new Snackbar();

export { snackbar };
