import { _ } from './types';

const addCartList = (product, cartList) => {
  let isExist = false;
  let count = 1;

  cartList.every((item) => {
    if (item.id === product.id) {
      isExist = true;
      count = Number(item.count) + 1;
      return false;
    }
    return true;
  });

  const targetProduct = { ...product, count, isChecked: true };

  if (isExist) return { type: _.ADD_EXIST_PRODUCT, payload: targetProduct };
  return { type: _.ADD_NEW_PRODUCT, payload: targetProduct };
};

const deleteCartItem = (productId) => ({ type: _.DELETE_PRODUCT, payload: productId });

const modifyCartItemCount = (productId, count) => ({
  type: _.MODIFY_PRODUCT_COUNT,
  payload: { productId, count },
});

export { addCartList, deleteCartItem, modifyCartItemCount };
